* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

body {
  --header-height: 60px;
  font-family: Roboto, sans-serif;
  font-size: 12px;
  font-style: normal;
  font-weight: 500;
}

header {
  justify-content: space-between;
  align-items: center;
  padding: 0 50px;
  display: flex;
}

.header__logo {
  flex-shrink: 0;
  width: 40px;
  height: 40px;
}

.header__logo img {
  width: 100%;
  height: 100%;
  margin-right: 10px;
}

.header__nav-list {
  flex-wrap: wrap;
  justify-content: space-between;
  list-style: none;
  display: flex;
}

.header__nav-item {
  margin-left: 20px;
}

.header__nav-item:first-of-type {
  margin-left: 0;
}

.header__nav-link {
  height: var(--header-height);
  line-height: var(--header-height);
  color: #000;
  text-transform: uppercase;
  white-space: nowrap;
  text-decoration: none;
  display: inline-block;
}

.is-active {
  color: #00acdc;
}

.is-active:after {
  content: "";
  background-color: #00acdc;
  border-radius: 2px;
  height: 4px;
  display: block;
  position: relative;
  top: -4px;
}

.header__nav-link:hover {
  color: #00acdc;
}
